import noneShow from '../../common/noneShow.vue'
import inputContact from '../../components/addressCheck/inputContact.vue'

export default{
	components: {
		noneShow,
		inputContact
	},
	data() {
		return {
			wxNotifyTemplatIds:[],
			
			pageIndex: 1,
			pageDatas:[],
			checkOrderIndex: "",
			checkOrder: "",
			gatherAmount:{
				allAmount:0.0
			},
			loadingText: "正在加载....",
			
			currentIndex: '',  //记录修改location的Index
			location:{
				id : 0,
				name:'',
				phoneNumber:'',
				buyerProvince:'',
				buyerCity:'',
				buyerDistrict:'',
				locationAddress:'',
				buyerFullAddress:'',
				buyerLongitude:'',
				buyerLatitude:'',
			},
			
			noneShowData:{
				detail:"购物车满满，幸福又美满。",
				toUrl:"/pages/tabBar/home/home",
				iconStr:"shouye10",
				buttonText:"去首页看看"
			}
		};
	},
	methods:{
		orderPageQuery() {
			this.checkLogin().then(val => {
				if(val === true){
					//获取用户信息
					let userInfo = this.store.getUserStore();
					let accountCode = '';
					let token = '';
						
					if(userInfo){
						accountCode = userInfo.accountCode;
						token = userInfo.token;
					}
					
					this.https({
						url: this.interfaces.initCart,  //POST json传输必须未转字符串
						method: 'POST',
						data: {
							'pageIndex':this.pageIndex, 
							'accountCode':accountCode,
							'token':token
						},
						header: {
							'content-type': 'application/json'
						},
						success: ((res) => { 
							this.wxNotifyTemplatIds = res.data.wxNotifyTemplatIds;
							
							if(res.data.dataVos.length <= 0){
								if(this.pageIndex == 1){
									this.pageDatas = res.data.dataVos;
								}
								this.pageIndex --;
								this.loadingText = "没有更多";
							}else{
								if(this.pageIndex == 1){
									this.pageDatas = res.data.dataVos;
								}else{
									this.pageDatas = this.pageDatas.concat(res.data.dataVos);	
								}
								//获取发布内容
								this.loadingText = "上拉获取更多";
							}
						})
					}); 
				}
			});
		},
		checkOrderClick(e){
			var index = e.currentTarget.id;
			const orderItem = this.pageDatas[index];
			
			this.checkOrderIndex = index;
			this.checkOrder = orderItem.orderNo;
			this.gatherAmount.allAmount = 0;
			for (let k in orderItem.orderGoods) {
				var singleAmount = parseFloat(orderItem.orderGoods[k].publishPrice * orderItem.orderGoods[k].buyCount).toFixed(2);
				this.gatherAmount.allAmount = parseFloat(this.gatherAmount.allAmount+singleAmount).toFixed(2);
			} 
		},
		showDetail(e){
			var id = e.currentTarget.id;
			var indexs = id.split('-');  
			var orderIndex = indexs[0];
			var detailIndex = indexs[1];
			const orderData = this.pageDatas[orderIndex];
			
			const goodsItem = orderData.orderGoods[detailIndex];
		
			uni.navigateTo({
				url:'../../detail/saleDetail?publishNo='+goodsItem.publishNo
			});	
			
		},
		subBuyCountClick(e){
			var id = e.currentTarget.id;
			var indexs = id.split('-');
			var orderIndex = indexs[0];
			var detailIndex = indexs[1];
			var buyCountemp = this.pageDatas[orderIndex].orderGoods[detailIndex].buyCount-1;
			
			if(buyCountemp <= 0){
				this.pageDatas[orderIndex].orderGoods[detailIndex].buyCount = 1;
				this.pageDatas[orderIndex].orderGoods[detailIndex].publishAmount = parseFloat(this.pageDatas[orderIndex].orderGoods[detailIndex].publishPrice).toFixed(2);
			}else{
				this.pageDatas[orderIndex].orderGoods[detailIndex].buyCount = buyCountemp;
				this.pageDatas[orderIndex].orderGoods[detailIndex].publishAmount = parseFloat(this.pageDatas[orderIndex].orderGoods[detailIndex].publishPrice * buyCountemp).toFixed(2);
				
				if(this.pageDatas[orderIndex].orderNo === this.checkOrder){
					this.gatherAmount.allAmount = parseFloat(this.gatherAmount.allAmount - this.pageDatas[orderIndex].orderGoods[detailIndex].publishPrice).toFixed(2);
				}
			}
			
			//提交后台处理
			this.updateBuyCount(orderIndex,detailIndex,this.pageDatas[orderIndex].orderGoods[detailIndex].buyCount);
			
		},
		addBuyCountClick(e){
			var id = e.currentTarget.id;
			var indexs = id.split('-');
			var orderIndex = indexs[0];
			var detailIndex = indexs[1];
			var buyCountemp = this.pageDatas[orderIndex].orderGoods[detailIndex].buyCount+1;
			
			if(buyCountemp <= 0){
				this.pageDatas[orderIndex].orderGoods[detailIndex].buyCount = 1;
				this.pageDatas[orderIndex].orderGoods[detailIndex].publishAmount = parseFloat(this.pageDatas[orderIndex].orderGoods[detailIndex].publishPrice).toFixed(2);
			}else{
				this.pageDatas[orderIndex].orderGoods[detailIndex].buyCount = buyCountemp;
				this.pageDatas[orderIndex].orderGoods[detailIndex].publishAmount = parseFloat(this.pageDatas[orderIndex].orderGoods[detailIndex].publishPrice * buyCountemp).toFixed(2);
				
				if(this.pageDatas[orderIndex].orderNo === this.checkOrder){
					this.gatherAmount.allAmount = parseFloat(this.gatherAmount.allAmount + this.pageDatas[orderIndex].orderGoods[detailIndex].publishPrice).toFixed(2);
				}
			}
			
			//提交后台处理
			this.updateBuyCount(orderIndex,detailIndex,this.pageDatas[orderIndex].orderGoods[detailIndex].buyCount);
		},
		updateBuyCount(orderIndex,detailIndex,buyCount){
			
			var orderNo = this.pageDatas[orderIndex].orderNo;
			var publishNo = this.pageDatas[orderIndex].orderGoods[detailIndex].publishNo;
			
			this.https({
				url: this.interfaces.updateBuyCount,  //POST json传输必须未转字符串 
				method: 'POST',
				data: {
					'orderNo':orderNo, 
					'publishNo':publishNo, 
					'buyCount':buyCount
				},
				header: {
					'content-type': 'application/json'
				},
				success: ((res) => { 
					if(res.data === true){
						
					}
				})
			});
		},
		deleteOrderGoods(e){
			this.checkLogin().then(val => {
				if(val === true){
					var id = e.currentTarget.id;
					var indexs = id.split('-');
					var orderIndex = indexs[0];
					var detailIndex = indexs[1];
					var orderNo = this.pageDatas[orderIndex].orderNo;
					var publishNo = this.pageDatas[orderIndex].orderGoods[detailIndex].publishNo;
					
					//获取用户信息
					let userInfo = this.store.getUserStore();
					let accountCode = '';
					let token = '';
						
					if(userInfo){
						accountCode = userInfo.accountCode;
						token = userInfo.token;
					}
					
					this.https({
						url: this.interfaces.deleteOrderGoods,  //POST json传输必须未转字符串
						method: 'POST',
						data: {
							'orderNo':orderNo, 
							'publishNo':publishNo, 
							'accountCode':accountCode,
							'token':token
						},
						header: {
							'content-type': 'application/json'
						},
						success: ((res) => { 
							if(res.data === true){
								//重新刷新下
								this.pageIndex = 1;
								this.gatherAmount.allAmount = 0;
								this.checkOrderIndex = "";
								this.checkOrder = "";
								this.orderPageQuery(); 
							}
						})
					}); 
				}
			});
		},
		buyerLocationPre(e){
			var that = this;
			if(that.wxNotifyTemplatIds == null || that.wxNotifyTemplatIds =='' || typeof(that.wxNotifyTemplatIds) == 'undefined'){
				that.buyerLocation(e);
			}else{
				// 用户没有点击“总是保持以上，不再询问”则每次都会调起订阅消息
				uni.requestSubscribeMessage({
				   tmplIds: that.wxNotifyTemplatIds,
				   success (res) {
					   console.log(res)
					   that.buyerLocation(e);
				   },
				   fail:(res) => {
					  console.log(res)
					   that.buyerLocation(e);
				   }
				});
			 }
		},
		buyerLocation(e){
			let _this = this;
			
			var index = e.currentTarget.id;
			_this.currentIndex = index;
			
			const locationData = _this.pageDatas[index];
			
			_this.location = {
				name:locationData.receiverName,
				phoneNumber:locationData.receiverPhoneNumber,
				
				id :locationData.receiverAddressId,
				buyerProvince:locationData.receiverProvince,
				buyerCity:locationData.receiverCity,
				buyerDistrict:locationData.receiverCounty,
				locationAddress:locationData.receiverAddress,
				buyerFullAddress:locationData.receiverFullAddress,
				buyerLongitude:locationData.receiverLongitude,
				buyerLatitude:locationData.receiverLatitude
			};
			
			//监听子页面数据
			_this.$eventHub.$on('fire',function(data){
				_this.location.id = data.id;
				_this.location.name = data.name;
				_this.location.phoneNumber = data.phoneNumber;
				_this.location.buyerProvince = data.province;
				_this.location.buyerCity = data.city;
				_this.location.buyerDistrict = data.district;
				_this.location.locationAddress = data.locationAddress;
				_this.location.buyerFullAddress = data.locationFullAddress;
				_this.location.buyerLongitude = data.longitude;
				_this.location.buyerLatitude = data.latitude;
				
				setTimeout(() => {
					//弹出联系人姓名 电话框
					_this.contactOption();
				},1000)
				
				//清除监听
				_this.$eventHub.$off('fire');
			});
			
			uni.navigateTo({
			 	url:'../../components/searchHeader/publishMap?myAddressType=receiver&allowEdit=true'+
				'&id='+_this.location.id+'&name='+_this.location.name+'&phoneNumber='+_this.location.phoneNumber+
				'&province='+_this.location.buyerProvince+'&city='+_this.location.buyerCity+'&district='+_this.location.buyerDistrict+'&locationAddress='+_this.location.locationAddress+
				'&locationFullAddress='+_this.location.buyerFullAddress+'&longitude='+_this.location.buyerLongitude+'&latitude='+_this.location.buyerLatitude,
			 	success: res => {},
			 	fail: () => {},
			 	complete: () => {
			 		
				}
			});	
		},
		contactOption(){
			this.$refs.inputContact.show();
		},
		contactConfirmCallback(data){
			let ads=data.inputInfo;
			
			this.location.name = ads.contactor;
			this.location.phoneNumber = ads.contactPhoneNumber;
			
			this.updateReceiverAddress();
			
		},
		updateReceiverAddress(){
			//获取修改的数据
			const orderData = this.pageDatas[this.currentIndex];
			
			const contactData = {
				orderNo: orderData.orderNo,
				contactor:this.location.name,
				contactPhoneNumber:this.location.phoneNumber,
				id : this.location.id,	
				province:this.location.buyerProvince,
				city:this.location.buyerCity,
				district:this.location.buyerDistrict,
				address:this.location.locationAddress,
				fullAddress:this.location.buyerFullAddress,
				longitude:this.location.buyerLongitude,
				latitude:this.location.buyerLatitude,
			};
						
			this.https({
				url: this.interfaces.updateReceiverAddress,  //POST json传输必须未转字符串
				method: 'POST',
				data: contactData,
				header: {
					'content-type': 'application/json'
				},
				success: ((res) => { 
					this.location.id = res.data;

					orderData.receiverName = this.location.name;
					orderData.receiverPhoneNumber = this.location.phoneNumber;
					orderData.receiverFullAddress = this.location.buyerFullAddress;
				})
			}); 
		},
		cartConfirm(){
			if(this.checkOrderIndex === "" || typeof (this.checkOrderIndex) === "undefined"
			|| this.checkOrder  === "" || typeof (this.checkOrder) === "undefined"){
				uni.showModal({
				    content: '请选择你要结算的订单！',
				    showCancel: false
				});
				return;
			}
			
			this.https({
				url: this.interfaces.cartConfirm,  //POST json传输必须未转字符串
				method: 'POST',
				data: {
					orderNo:this.checkOrder
				},
				header: {
					'content-type': 'application/json'
				},
				success: ((res) => { 
					var orderNoT = this.checkOrder;
					
					//重新刷新下
					this.pageIndex = 1;
					this.gatherAmount.allAmount = 0;
					this.checkOrderIndex = "";
					this.checkOrder = "";
					this.orderPageQuery(); 
					
					uni.navigateTo({
						url:'../../pay/publishPay?busNo='+orderNoT+"&busType=order",
						success: res => {},
						fail: () => {},
						complete: () => {
							
						}
					});
				})
			}); 
		},
		async checkLogin(){
			//获取同步获取当前storeage的相关信息
			let fromUrl = "/pages/tabBar/cart/cart";
			let toUrl = ""; 
			let authFinishedToUrl = '/pages/tabBar/cart/cart'; //授权完成后跳转页面
			let refuseUrl = '/pages/tabBar/cart/cart'
			let authUrl = "/pages/auth/login";
			let tokenVerify = true;
			return await this.login.checkLogin(fromUrl,toUrl,refuseUrl,authUrl,tokenVerify,authFinishedToUrl);
		},
		/* nextPageCloseToUrl_pay(){
			uni.redirectTo({
				url:'../../user/userBuyOrder'
			});
		} */
	},
	onTabItemTap(){
		/* uni.showLoading({title:"加载中..."})
		this.pageIndex = 1;
		this.gatherAmount.allAmount = 0;
		this.checkOrderIndex = "";
		this.checkOrder = "";
		this.orderPageQuery(); */
	},
	onLoad() {
		//消息通知
		var notifyStore = this.store.getNotifyStore();
		notifyStore.BUYER_BUY_ORDER_ADD_CART_NOTIFY = "";	
		this.store.setNotifyStore(notifyStore);
		
		console.log('The page is be onLoad');
		uni.showLoading({title:"加载中..."})
		this.pageIndex = 1;
		this.gatherAmount.allAmount = 0;
		this.checkOrderIndex = "";
		this.checkOrder = "";
		this.orderPageQuery(); 
	},
	mounted() {
		
	},
	onPullDownRefresh(){
		uni.showLoading({title:"加载中..."})
		setTimeout(() => {
			this.pageIndex = 1;
			this.gatherAmount.allAmount = 0;
			this.checkOrderIndex = "";
			this.checkOrder = "";
			this.pageDatas = [];
			this.loadingText = "加载中...";
			this.orderPageQuery();
			uni.stopPullDownRefresh();
		},1000)
	},
	// 上拉加载
	onReachBottom(){
		this.pageIndex++;
		this.orderPageQuery();
		console.log('start onReachBottom');
	},
}

